import { Component, OnInit, Input } from '@angular/core';
import { PiService } from '../pi.service';

@Component({
  selector: 'app-pin-pad',
  templateUrl: './pin-pad.component.html',
  styleUrls: ['./pin-pad.component.css'],
  providers: [PiService]
})
export class PinPadComponent implements OnInit {

  activatedPins: any[] = [];
  deactivatedPins: Array<number> = [];

  constructor(public svc: PiService) { }

  ngOnInit() {
    this.svc.loadActivatedPins()
      .then(pins => {
        this.activatedPins = pins;
        for (let i = 0; i < 28; i++) {
          if (this.activatedPins.filter(p => p.number === i).length === 0) {
            this.deactivatedPins.push(i);
          }
        }
      });
  }

  toggle(pin: any): void {
    if (pin.on) {
      this.turnOff(pin);
    } else {
      this.turnOn(pin);
    }
  }
  turnOn(pin: any): void {
    this.svc.turnOnPin(pin.number)
      .then(() => pin.on = true);
  }

  turnOff(pin: any): void {
    this.svc.turnOffPin(pin.number)
      .then(() => pin.on = false);
  }
  deactivate(event, pin): void {
    this.svc.deInitializePin(pin.number)
      .then(() => {
        this.activatedPins.splice(this.activatedPins.indexOf(pin), 1);
        this.deactivatedPins.push(pin.number);
        this.deactivatedPins = this.deactivatedPins.sort((n1, n2) => n1 - n2);
      });
    event.stopPropagation();
  }
  activate(pinnumber): void {
    this.svc.initializePin(pinnumber)
      .then(() => {
        this.svc.loadPinDetails(pinnumber)
          .then(pin => {
            this.activatedPins.push(pin);
            this.deactivatedPins.splice(this.deactivatedPins.indexOf(pinnumber), 1);
          });
      });
  }

  handleError(error): void {
    console.log(JSON.stringify(error));
  }
}



